<template>
  <div class="order-info">
    <el-table
      :data="orderInfoList"
      :empty-text="hasListInfo"
      border
      style="width: 100%"
    >
      <el-table-column prop="orderNum" label="订单号" align="center" />
      <el-table-column prop="orderName" label="订单名称" align="center" />
      <el-table-column prop="type" label="订单类型" align="center" />
      <el-table-column prop="price" label="单价" align="center" />
      <el-table-column prop="count" label="数量" align="center" />
      <el-table-column prop="actualReceipts" label="实收款" align="center" />
      <el-table-column prop="status" label="交易状态" align="center" />
      <el-table-column prop="saleStatus" label="售后状态" align="center" />
      <el-table-column prop="time" label="订单创建时间" align="center" />
    </el-table>
  </div>
</template>

<script>
import { getOrderList } from '@/api/assistantManage'
export default {
  props: {
    user: {
      type: String,
      default: ''
    }
  },
  data() {
    return {
      orderInfoList: [],
      hasListInfo: '暂无订单信息'
    }
  },
  watch: {
    user(value) {
      if (value) this.getOrderList()
    }
  },
  computed: {
    getTradeStatusCh() {
      return function(status) {
        if (status === 'SUCCESS') return '交易成功'
        if (status === 'PENDING') return '待支付'
        if (status === 'CLOSED') return '交易关闭'
      }
    },
    getSaleStatusCh() {
      return function(status) {
        if (status === 'PENDING_TRANSFER_ACCOUNT') return '退款待到账'
        if (status === 'FAIL_TRANSFER_ACCOUNT') return '退款到账失败'
        if (status === 'SUCCESS_MANUAL') return '退款成功(人工)'
        if (status === 'SUCCESS_AUTO') return '退款成功(自动)'
      }
    }
  },
  methods: {
    async getOrderList() {
      const { code, data } = await getOrderList(this.user)
      if (code === 10000) {
        this.orderInfoList = data.list.map(v => {
          return {
            orderNum: v._id,
            orderName:
              v.order_type === 'FdServiceType'
                ? v.order_logic_data.product_info.title +
                  v.order_logic_data.product_info.sub_title
                : '单次问诊',
            type: v.order_type === 'FdServiceType' ? '家庭医生' : '单次问诊',
            price: v.cost_price ? v.cost_price / 100 : 0,
            count: 1,
            actualReceipts: v.pay_price ? v.pay_price / 100 : 0,
            status: this.getTradeStatusCh(v.trade_status),
            saleStatus: this.getSaleStatusCh(v.refund_status) || '/',
            time: this.formatTime(v.created_at)
          }
        })
      }
    }
  }
}
</script>

<style scoped>
.order-info {
  margin-top: 12px;
}
</style>
