<template>
  <div class="user-info">
    <div class="head-info">
      <img class="avatar" :src="avatar" alt="头像" />
      <div :key="i" class="info-wrap" v-for="(index, i) in rowCount">
        <div class="info-item">
          {{
            headInfo[index * 2 - 2] !== undefined
              ? headInfo[index * 2 - 2].key + '：'
              : ''
          }}
          {{
            headInfo[index * 2 - 2] !== undefined
              ? headInfo[index * 2 - 2].value
              : ''
          }}
        </div>
        <div class="info-item">
          {{
            headInfo[index * 2 - 1] !== undefined
              ? headInfo[index * 2 - 1].key + '：'
              : ''
          }}
          {{
            headInfo[index * 2 - 1] !== undefined
              ? headInfo[index * 2 - 1].value
              : ''
          }}
        </div>
      </div>
    </div>
    <el-tabs v-model="activeName" @tab-click="tabClick">
      <el-tab-pane label="信息详情" name="info">
        <h3 class="title">基本信息</h3>
        <vertical-table
          :table-data="tableData"
          :table-style="{ width: '100%' }"
        />
        <h3 class="title">宝宝信息</h3>
        <el-table :data="babyList" border style="width: 100%">
          <el-table-column prop="name" label="宝宝昵称" align="center" />
          <el-table-column prop="monthAge" label="宝宝月龄" align="center" />
          <el-table-column
            prop="birthday"
            label="宝宝出生日期"
            align="center"
          />
          <el-table-column prop="gender" label="宝宝性别" align="center" />
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="跟进详情" name="follow">
        <follow />
      </el-tab-pane>
      <el-tab-pane label="订单详情" name="order">
        <order :user="userId" />
      </el-tab-pane>
    </el-tabs>
  </div>
</template>

<script>
import VerticalTable from '@/components/VerticalTable/index'
import Follow from './components/follow'
import Order from './components/order'
import { getUserInfo } from '@/api/assistantManage'
import { getAge } from '@/utils'
export default {
  components: {
    VerticalTable,
    Follow,
    Order
  },
  data() {
    return {
      tableData: [],
      headInfo: [],
      babyList: [],
      avatar: '',
      activeName: 'info',
      userId: ''
    }
  },
  computed: {
    rowCount() {
      return Math.ceil(this.headInfo.length / 2)
    },
    getOrderType() {
      return function(type) {
        if (type === 'out_service') return '服务结束'
        if (type === 'in_service') return '服务中'
        if (type === 'pending_service') return '待激活'
        if (type === 'empty' || !type) return '未签约'
        if (type === 'pending_active') return '待激活'
      }
    },
    getMonthAge() {
      return function(birthday) {
        const obj = getAge(birthday)
        return (
          `${obj.year ? obj.year + '年' : ''}${
            obj.month ? obj.month + '月' : ''
          }${obj.date ? obj.date + '日' : ''}` || '1天'
        )
      }
    },
    getUserTypeInfo() {
      return function(type) {
        if (type) {
          return type.type + (type.level ? '-' + type.level : '')
        }
        return ''
      }
    },
    getFollowInfo() {
      return function(current_count, count, time) {
        return (
          (current_count > 0 ? '跟进中' : '未跟进') +
          ' | ' +
          (current_count || 0) +
          '/' +
          (count || 0) +
          ' | ' +
          (time ? this.formatTime(time) : '/')
        )
      }
    },
    getUserSource() {
      return function(type) {
        if (type && type.first_name) {
          return `${type.first_name}-${type.second_name}-${type.three_name}`
        }
        return ''
      }
    }
  },
  created() {
    this.getUserInfo()
  },
  methods: {
    tabClick(tab, event) {
      //
    },
    async getUserInfo() {
      const { code, data } = await getUserInfo(this.$route.params.id)
      if (code === 10000) {
        if (data) {
          this.avatar = data.avatar
          this.userId = data.user || ''
          this.headInfo = [
            { key: '用户昵称', value: data.name },
            {
              key: '当前负责人',
              value:
                data.handler_name +
                  (data.nickname ? `(${data.nickname})` : '') || '/'
            },
            {
              key: '用户类型',
              value: this.getUserTypeInfo(data.user_type) || '/' || '/'
            },
            {
              key: '跟进情况',
              value: this.getFollowInfo(
                data.follow_current_times,
                data.follow_total_times,
                data.last_follow_at
              )
            },
            { key: '用户诉求', value: data.user_remark || '/' }
          ]
          this.tableData = [
            { key: '用户昵称', value: data.name },
            {
              key: '用户性别',
              value:
                data.gender === 1 ? '男' : data.gender === 2 ? '女' : '未知'
            },
            { key: '首次添加时间', value: this.formatTime(data.created_at) },
            { key: '更新时间', value: this.formatTime(data.updated_at) },
            {
              key: '用户类型',
              value: this.getUserTypeInfo(data.user_type) || '/'
            },
            {
              key: '当前负责人',
              value:
                data.handler_name +
                  (data.nickname ? `(${data.nickname})` : '') || '/'
            },
            {
              key: '首次负责人',
              value:
                data.first_handler_name +
                  (data.first_nickname ? `(${data.first_nickname})` : '') || '/'
            },
            { key: '用户来源', value: this.getUserSource(data.user_source) },
            { key: '来源场景', value: data.user_remark || '/' },
            { key: '用户诉求', value: data.from_scene || '/' },
            { key: '签约次数', value: data.order_count || 0 },
            {
              key: '跟进状态',
              value: data.follow_current_times > 0 ? '跟进中' : '未跟进'
            },
            { key: '阶段跟进次数', value: data.follow_current_times || 0 },
            {
              key: '最近一次跟进时间',
              value: data.last_follow_at
                ? this.formatTime(data.last_follow_at)
                : '/'
            },
            { key: '跟进总数', value: data.follow_total_times || 0 }
          ]
          if (data.member) {
            this.babyList = data.member.map(v => {
              return {
                name: v.name,
                monthAge: this.getMonthAge(v.birthday),
                birthday: this.formatTime(v.birthday),
                gender: v.gender === 1 ? '男' : v.gender === 2 ? '女' : '未知'
              }
            })
          }
        }
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.user-info {
  padding: 0 20px;
  .head-info {
    display: flex;
    margin-bottom: 20px;
    .info-wrap {
      display: flex;
      flex-direction: column;
      justify-content: space-around;
      margin-right: 60px;
      max-width: 350px;
      .info-item {
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
      }
    }
    .avatar {
      margin-right: 50px;
      width: 102px;
      height: 102px;
    }
  }
  .title {
    margin: 16px 0;
  }
}
</style>
