<template>
  <table class="mailTable" :style="styleObject" v-if="s_showByRow">
    <tr v-for="(index, i) in rowCount" :key="i">
      <td class="column">{{ tableData[index * 4 - 4].key }}</td>
      <td>{{ tableData[index * 4 - 4].value }}</td>
      <td class="column">
        {{
          tableData[index * 4 - 3] !== undefined
            ? tableData[index * 4 - 3].key
            : ''
        }}
      </td>
      <td>
        {{
          tableData[index * 4 - 3] !== undefined
            ? tableData[index * 4 - 3].value
            : ''
        }}
      </td>
      <td class="column">
        {{
          tableData[index * 4 - 2] !== undefined
            ? tableData[index * 4 - 2].key
            : ''
        }}
      </td>
      <td>
        {{
          tableData[index * 4 - 2] !== undefined
            ? tableData[index * 4 - 2].value
            : ''
        }}
      </td>
      <td class="column">
        {{
          tableData[index * 4 - 1] !== undefined
            ? tableData[index * 4 - 1].key
            : ''
        }}
      </td>
      <td>
        {{
          tableData[index * 4 - 1] !== undefined
            ? tableData[index * 4 - 1].value
            : ''
        }}
      </td>
    </tr>
  </table>

  <table class="mailTable" :style="styleObject" v-else>
    <tr v-for="(index, i) in rowCount" :key="i">
      <td class="column">{{ tableData[index - 1].key }}</td>
      <td>{{ tableData[index - 1].value }}</td>
      <td class="column">
        {{
          tableData[rowCount + index - 1] !== undefined
            ? tableData[rowCount + index - 1].key
            : ''
        }}
      </td>
      <td>
        {{
          tableData[rowCount + index - 1] !== undefined
            ? tableData[rowCount + index - 1].value
            : ''
        }}
      </td>
    </tr>
  </table>
</template>

<script>
export default {
  data() {
    return {
      styleObject: {},
      s_showByRow: true
    }
  },
  props: ['tableData', 'tableStyle', 'showByRow'],
  computed: {
    rowCount: function() {
      return Math.ceil(this.tableData.length / 4)
    }
  },
  created() {
    this.styleObject = this.tableStyle
    if (this.showByRow !== undefined) {
      this.s_showByRow = this.showByRow
    }
  }
}
</script>

<style>
.mailTable,
.mailTable tr,
.mailTable tr td {
  text-align: center;
  border: 1px solid #e6eaee;
}
.mailTable {
  margin-bottom: 38px;
  font-size: 12px;
  color: #71787e;
  border-collapse: collapse;
}
.mailTable tr td {
  padding: 0 10px;
  width: 150px;
  height: 35px;
  line-height: 35px;
  box-sizing: border-box;
  border: 1px solid #e6eaee;
}
.mailTable tr td.column {
  color: #393c3e;
  background-color: #eff3f6;
}
</style>
