<template>
  <div class="follow-detail">
    <el-form
      ref="followForm"
      label-position="left"
      label-width="70px"
      :model="followForm"
      inline
    >
      <el-form-item label="跟进时间" class="search-item">
        <el-date-picker
          v-model="followForm.time"
          type="daterange"
          align="right"
          unlink-panels
          range-separator="-"
          start-placeholder="起始跟进时间"
          end-placeholder="截至跟进时间"
          value-format="yyyy-MM-dd"
        />
      </el-form-item>
      <el-form-item label="跟进人" class="search-item">
        <el-select
          filterable
          allow-create
          v-model="followForm.followPerson"
          clearable
          placeholder="请选择跟进人"
        >
          <el-option
            v-for="item in followPersonList"
            :key="item"
            :label="item"
            :value="item"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="签约状态" class="search-item">
        <el-select
          v-model="followForm.status"
          clearable
          placeholder="请选择签约状态"
        >
          <el-option
            v-for="item in statusList"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          />
        </el-select>
      </el-form-item>
      <el-form-item label="用户类型" class="search-item">
        <el-select
          v-model="followForm.type"
          clearable
          placeholder="请选择用户类型"
        >
          <el-option
            v-for="item in typeList"
            :key="item"
            :label="item"
            :value="item"
          />
        </el-select>
      </el-form-item>
      <el-form-item style="float: right">
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
        <el-button type="primary" class="select-btn" @click="resetForm"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <el-table
      :data="followInfoList"
      :empty-text="hasListInfo"
      border
      style="width: 100%"
    >
      <el-table-column prop="num" label="序号" align="center" />
      <el-table-column prop="actionName" label="跟进动作名称" align="center" />
      <el-table-column prop="followStatus" label="跟进类型" align="center" />
      <el-table-column prop="followPerson" label="操作人" align="center" />
      <el-table-column prop="type" label="用户类型" align="center" />
      <el-table-column prop="status" label="签约状态" align="center" />
      <el-table-column prop="time" label="跟进时间" align="center" />
    </el-table>
    <div class="pagination">
      <el-pagination
        :current-page="currentPage"
        :page-sizes="[10, 20, 50]"
        :page-size="pageSize"
        layout="total, sizes, prev, pager, next, jumper"
        :total="count"
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
      />
    </div>
  </div>
</template>

<script>
import {
  getFollowLog,
  getAdminList,
  getUserTypeList
} from '@/api/assistantManage'
export default {
  data() {
    return {
      currentPage: 1,
      pageSize: 10,
      count: 0,
      followForm: {
        time: ['', ''],
        followPerson: '',
        status: '',
        type: ''
      },
      followPersonList: [],
      statusList: [
        { value: 'empty', label: '未签约' },
        { value: 'pending_service', label: '待激活' },
        { value: 'in-service', label: '服务中' },
        { value: 'out-service', label: '已过期' }
      ],
      typeList: [],
      followInfoList: [],
      hasListInfo: '暂无跟进信息'
    }
  },
  computed: {
    translateType() {
      return function(type) {
        if (type === 'first') return '首次跟进'
        if (type === 'announcement') return '活动通知'
        if (type === 'take_care') return '回访'
        if (type === 'visit') return '日常关心'
      }
    },
    getOrderType() {
      return function(type) {
        if (type === 'out_service') return '服务结束'
        if (type === 'in_service') return '服务中'
        if (type === 'pending_service') return '待激活'
        if (type === 'empty' || !type) return '未签约'
        if (type === 'pending_active') return '待激活'
      }
    },
    getUserTypeInfo() {
      return function(type) {
        if (type && type.type) {
          return type.type + (type.level ? '-' + type.level : '')
        }
        return ''
      }
    }
  },
  created() {
    this.getFollowLog()
    this.getAdminList()
    this.getUserTypeList()
  },
  methods: {
    onSubmit() {
      this.getFollowLog(this.followForm)
    },
    handleSizeChange(val) {
      this.pageSize = val
      this.getFollowLog(this.followForm)
    },
    handleCurrentChange(val) {
      this.currentPage = val
      this.getFollowLog(this.followForm)
    },
    resetForm() {
      this.followForm = {
        time: ['', ''],
        followPerson: '',
        status: '',
        type: ''
      }
    },
    async getAdminList() {
      const { code, data } = await getAdminList({ pageSize: 0 })
      if (code === 10000) {
        this.followPersonList = data.map(v => {
          return v.name
        })
      }
    },
    async getFollowLog(search) {
      let params = {
        page: this.currentPage,
        pageSize: this.pageSize,
        external_userid: this.$route.params.id
      }
      if (search) {
        if (search.status) params.order_status = search.status
        if (search.type) params.user_type = search.type
        if (search.followPerson) params.helper_name = search.followPerson
        if (search.time && search.time[0] && search.time[1])
          params.created_at = search.time[0] + ' | ' + search.time[1]
      }
      const { code, data } = await getFollowLog(params)
      if (code === 10000) {
        this.count = data.count
        this.followInfoList = data.list.map((v, index) => {
          return {
            num: (this.currentPage - 1) * this.pageSize + index + 1,
            actionName: v.name,
            followStatus: this.translateType(v.type) || '/',
            followPerson: v.helper_name || '/',
            type: this.getUserTypeInfo(v.user_type) || '/',
            status: this.getOrderType(v.order_status) || '/',
            time: this.formatTime(v.follow_at) || '/'
          }
        })
      }
    },
    async getUserTypeList() {
      const { code, data } = await getUserTypeList({ pageSize: 0 })
      if (code === 10000) {
        this.typeList = data.list
          .map(item => {
            return item.type + (item.level ? '-' + item.level : '')
          })
          .filter((v, index, arr) => {
            return arr.indexOf(v) === index
          })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.follow-detail {
  display: flex;
  flex-wrap: wrap;
  margin-top: 10px;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    color: #17233d;
    &:not(:nth-child(4)) {
      margin-right: 50px;
    }
    /deep/input {
      width: 220px;
      height: 32px;
      line-height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0;
    padding: 20px;
    width: 100%;
    border: 1px solid #ebeef5;
    .title {
      display: flex;
      justify-content: space-between;
      line-height: 32px;
      /deep/button {
        padding: 6px 20px;
        height: 32px;
      }
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    white-space: nowrap;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
  }
  .center {
    text-align: center;
  }
  .pagination {
    width: 100%;
    text-align: center;
  }
}
</style>
